import React, {Component} from 'react'
import {importAll} from "utils";
import { withRouter } from 'react-router-dom';

class Search extends Component {

    constructor(props) {
        super(props);
    }

    _handleSearch = (e) => {
        if(e.key === 'Enter') {
            this.props.history.push(`/search?query=${e.target.value}`);
        }
    }

    render() {
        const webpackContext = require.context('assets/img/lobby', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);
        const { phone } = this.props;
        return (
            <div className="nav-search" htmlFor="search">
                <img className="nav-search_icon" src={phone? images['search_dark_u819.png'] :images['search_u29.png']}/>
                <input id="search" className="nav-search_input" placeholder="输入游戏名称或玩家" onKeyDown={(e) => this._handleSearch(e)}/>
                <label className="nav-search--focus"></label>
            </div>
        )
    }
}

export default withRouter(Search);