import React, { Component } from 'react';
import { Button } from 'antd';

class QButton extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const {
            content,
            size
        } = this.props;
        return (
            <Button className="q-main" type="primary" shape="round" size={size}>
                {content}
            </Button>
        )
    }
}

export default QButton;