import React, {Component} from 'react';
import {Icon} from 'antd';
import ReactDOM from 'react-dom';

import {importAll} from 'utils';

import Search from 'components/partial/search/Search';

class Header extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const webpackContext = require.context('assets/img/lobby', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);

        return (
            <header className="header">
                <div className="header-top">
                    <div className="logo">
                        {/*<img className="logo-img" src={require(`assets/img/lobby/logo_img_u24.svg`)}/>*/}
                        <img className="logo-img" src={images['logo_img_u24.svg']}/>
                        <span className="logo-name">AVATAR</span>
                    </div>

                    <div className="operator">
                        <div className="operator-min">
                            <img src={images['operator_min_u21.png']}/>
                        </div>
                        <div className="operator-max">
                            <img src={images['operator_max_u22.png']}/>
                        </div>
                        <div className="operator-x">
                            <img src={images['operator_close_u20.png']}/>
                        </div>
                    </div>
                </div>

                <div className="header-botom nav">
                    <div className="nav-router">
                        <Icon type="arrow-left"/>
                        <Icon type="arrow-right"/>
                        {/*<img className="nav-router_back" src={images['nav_back_u27.png']}/>*/}
                        {/*<img className="nav-router_forward" src={images['nav_forward_u28.png']}/>*/}
                    </div>
                    <div className="nav-crumbs">
                        <ul>
                            <li className="nav-crumbs_crumb nav-crumbs_crumb--active">
                                <a className="nav-crumbs_crumb-name">游戏大厅</a>
                            </li>
                            <li className="nav-crumbs_crumb">
                                <a className="nav-crumbs_crumb-name">社区</a>
                            </li>
                            <li className="nav-crumbs_crumb">
                                <a className="nav-crumbs_crumb-name">个人中心</a>
                            </li>
                        </ul>
                    </div>

                    <Search />

                    <div className="nav-social">
                        <img className="nav-social_mail" src={images['social_mail_u17.png']}/>
                        <img className="nav-social_chat" src={images['social_chat_u16.png']}/>
                    </div>

                    <div className="nav-profile">
                        <img className="nav-profile_avatar" src={images['profile_avatar_u15.svg']}/>
                        <div className="nav-profile_detail">
                            <div className="nav-profile_detail-name">
                                JRlazhu
                            </div>
                            <div className="nav-profile_detail-id">
                                25462154
                            </div>
                        </div>
                        <img className="nav-profile_more" src={images['down_triangle_u18.png']}/>
                    </div>

                </div>
            </header>
        )
    }
}

export default Header;