import React, { Component } from 'react';

import { importAll } from "utils";

class GameCarousel extends Component {

    render() {
        const webpackContext = require.context('assets/img/lobby', false, /\.(png|jpe?g|svg)$/);
        const webpackContext1 = require.context('assets/img/gamedetail', false, /\.(png|jpe?g|svg)$/);
        const images = importAll(webpackContext);
        const images1 = importAll(webpackContext1);
        return (
          <div className="game-carousel">
               <div className="game-carousel_banner"
                             style={{backgroundImage: `url(${images['carousel_item_u587.svg']})`}}></div>

              <div className="game-carousel_list">
                  <div className="sign-x">
                      <img className="sign-x-left" src={images1['carousel_right_u77.png']}/>
                  </div>

                  <div className="game-carousel_list-box scroll-bar">
                      {
                          new Array(10).fill(1).map(() => (
                              <div className="game-carousel_list-box_item">
                                  <div className="game-carousel_list-box_item-img"
                                       style={{backgroundImage: `url(${images['carousel_item_u587.svg']})`}}></div>
                              </div>
                          ))
                      }
                  </div>

                  <div className="sign-x">
                      <img className="sign-x-right" src={images1['carousel_right_u77.png']}/>
                  </div>

              </div>
          </div>
        );
    }
}

export default GameCarousel;